import React from 'react';
import { Box } from '@mui/material';
import Plan from './Plan';
import { PLANS } from './plans.object';
import {
  PlansTitle,
  PlansSubtitle,
  PlansContent,
  PlansDescriptionText,
} from './styles';

export const Plans = () => (
  <Box sx={{ py: 2, px: 2 }}>
    <PlansTitle sx={{ my: 2 }}>Conhea nossos planos de anncio</PlansTitle>
    <PlansSubtitle sx={{ my: 2 }}>
      Escolha o que melhor se encaixa para voc
    </PlansSubtitle>

    <PlansContent>
      {PLANS.map((plan, i) => (
        <Plan key={plan.id} plan={plan} i={i} />
      ))}
    </PlansContent>

    <Box
      sx={{
        px: { xs: 2, md: 10 },
        mt: 1,
        mb: 2,
      }}
    >
      <PlansDescriptionText sx={{ my: 2 }}>
        <b>Gratuito:</b> Recomendado para iniciantes,  uma tima forma de
        testar a plataforma. Voc pode testar a plataforma. Voc pode criar um
        anncio gratuto da sua propriedade, com as informaes gerais do
        imvel, e dura 7 dias.
      </PlansDescriptionText>

      <PlansDescriptionText sx={{ my: 2 }}>
        <b>Premium:</b> Recomendado para quem j tem contedos da propriedade
        como fotos, detalhes do imvel como tipo de solo, atividades,
        topografia. Ele custa R$ 50,00 e seu anncio ganha mais visibilidade
        dentro da plataforma.
      </PlansDescriptionText>

      <PlansDescriptionText sx={{ my: 2 }}>
        <b>Profissional:</b> Entre em contato conosco para conversarmos e
        conhecermos melhor o seu imvel rural. Iremos at a propriedade,
        colheremos as informaes mais importanjtes dela e faremos excelentes
        gravaes explorando o imvel atravs de dones de ponta. Essa  a
        maneira mais fcil de vender.
      </PlansDescriptionText>
    </Box>
  </Box>
);
