import React from 'react';
import LogoImg from 'common/static/Logo.svg';
import { HomePageTemplate } from 'common/components';
import classes from './About.module.scss';

export const About = () => {
  return (
    <HomePageTemplate>
      <div className={classes.about}>
        <h1>Quem somos?</h1>

        <div className={classes.card}>
          <div className={classes.row}>
            <div>
              <p>
                O Brasil  o quinto pas com maior extenso territorial do
                mundo, com cerca de 152,5 milhes de hectares (17,9% do
                territrio brasileiro) sendo terras e reas agricultveis.
                Segundo o Sistema Nacional de Cadastro Rural (SNCR), em 2013 o
                pas possua cerca de 5.6 milhes de imveis rurais, ocupando
                60% da rea todal do Brasil.
              </p>
              <p>
                Analisando e estudando como  feito atualmente a compra e venda
                desses imveis, a ideia da Terrafinder  facilitar esse processo
                que hoje  feito muitas vezes de maneira rudimentar e por meios
                como jornais, contatos pessoais, explorao e alguns canais
                digitais.
              </p>
              <p>
                Com uma onda de digitalizao que hoje impacta at as geraes
                mais velhas, a ideia  construir um sistema que atenda as
                necessidades do pblico mais velho, mas que tambm d liberdade
                para geraes mais novas poderem utilizar.
              </p>
              <p>
                A Terrafinder  uma startup que busca conectar e facilitar a
                vida de proprietrios de imveis rurais e de interessados em
                adquirir tais imveis. Atravs de uma plataforma digital a ideia
                 permitir aos proprietrios desses imveis, anunciarem e
                promover suas vendas com facilidade e visibilidade para pessoas
                que tem interesse em comprar tais propriedades.
              </p>
            </div>

            <div>
              <img src={LogoImg} alt="" />
            </div>
          </div>

          <div className={classes.aboutValues}>
            <h2>Misso</h2>
            <p>Conectar compradores e vendedores de imveis rurais</p>

            <h2>Viso</h2>
            <p>Ser uma plataforma completa de solues para o agrobusiness</p>

            <h2>Valores</h2>
            <ul>
              <li>Integridade</li>
              <li>Lealdade</li>
              <li>Compromisso</li>
              <li>Smart Work</li>
              <li>Unio e Equipe</li>
            </ul>
          </div>
        </div>
      </div>
    </HomePageTemplate>
  );
};
